.call-to-action {
  height: 200px;
  background-color: $clr4;
  color: $clr2;
}

.bottom-left-picture-desktop-container {
  display: flex;
  width: 200px;
  height: 144px;
  position: absolute;
  align-self: flex-end;
}

.bottom-left-picture-mobile-container {
  display: none;
}

.call-to-action-container {
  height: 100%;
  width: 100%;
  align-items: center;
}

.call-to-action-title {
  width: 504px;
  height: 51px;
  margin-left: 98px;
  margin-right: 237px;
}

@include for-tablet-layout {
  .call-to-action {
    height: 200px;
  }

  // .call-to-action-container {
  //   justify-content: center;
  // }

  .call-to-action-title {
    width: 339px;
    height: 34px;
    // line-height: 32px;
    // text-align: center;
    margin-left: 58px;
    margin-right: 113px;
  }

  .bottom-left-picture-desktop-container {
    display: none;
  }

  .bottom-left-picture-mobile-container {
    display: flex;
    position: absolute;
    align-self: flex-end;
    width: 198px;
    height: 122px;
  }
}

@include for-mobile-layout {
  .call-to-action {
    height: 302px;
  }

  .call-to-action-container {
    flex-direction: column;
    align-items: center;
  }

  .call-to-action-title {
    width: 100%;
    // height: 34px;
    // line-height: 32px;
    text-align: center;
    margin-top: 83px;
    margin-bottom: 54px;
    margin-left: 0;
    margin-right: 0;
  }

}
