.hero {
  background-color: $clr1;
  height: 506px;
  width: 100%;
}

.hero-left-pic-container {
  display: flex;
  padding-top: 56px;
  position: absolute;
}

.hero-container {
  padding-top: 56px;
}

.hero-title {
  width: 635px;
  height: 185px;
  margin-right: 30px;
}

.hero-emphasis {
  color: $clr4;
}

.hero-text-container {
  justify-content: space-between;
}

.hero-rectangle-container {
  display: flex;
}

.hero-text {
  width: 445px;
  height: 105px;
  justify-self: flex-end;
  font-size: rem(17px);
}

.hero-bottom-container-desktop {
  display: flex;
  justify-content: flex-end;
  align-content: flex-end;
  margin-top: 165px;
}

.hero-bottom-container-mobile {
  display: none;
}

@include for-tablet-layout {
  .hero {
    height: 524px;
    flex-direction: column;
  }

  .hero-container {
    padding-top: 48px;
    flex-direction: column;
    align-items: center;
  }

  .hero-title {
    height: 107px;
    width: 397px;
    text-align: center;
    margin-right: 0;
    margin-bottom: 29px;
    align-self: center;
  }

  .hero-text-container {
    justify-content: center;
  }

  .hero-rectangle-container {
    display: none;
  }

  .hero-text {
    width: 475px;
    height: 79px;
    font-size: rem(15px);
    text-align: center;
    line-height: 28px;
  }

  .hero-left-pic-container {
    display: none;
    position: initial;
    img {
      display: none;
    }
  }

  .hero-bottom-container-desktop{
    align-content: center;
    justify-content: center;
  }
}

@include for-mobile-layout {
  .hero {
    height: 439px;
  }

  .hero-container {
    padding-top: 32px;
    flex-direction: column;
  }

  .hero-title {
    height: 75px;
    width: 235px;
    margin-bottom: 23px;

    h1 {
      font-size: rem(38px);
      line-height: 40px;
    }
  }

  .hero-text {
    width: 100%;
    height: 107px;
  }

  .hero-bottom-container-desktop {
    display: none;
  }

  .hero-bottom-container-mobile {
    display: flex;
    width: 100%;
    padding: 0 9px;
    margin-top: 102px;

    img {
      width: 100%;
      height: 100px;
    }
  }
}
